import React, { useEffect } from 'react'
import { Link } from 'react-router-dom';
// import MessageBox from "../components/MessageBox";
import { useDispatch, useSelector } from 'react-redux';
import { addToCart, removeFromCart } from '../actions/cartAction';

function CartScreen(props) {
    const productId = props.match.params.id;
    const qty = props.location.search ? Number(props.location.search.split('=')[1]) : 1;
    const cart = useSelector(state => state.cart)
    const { cartItems } = cart;
    const dispatch = useDispatch();

    useEffect(() => {
        if (productId) {
            dispatch(addToCart(productId, qty));
        }
    }, [dispatch, productId, qty]);


    //removeFromCartHandler
    const removeFromCartHandler = (id) => {
        dispatch(removeFromCart(id));
    }

    //checkoutHandler
    const checkoutHandler = () => {
        props.history.push('/signin?redirect=shipping');
    }

    const subtotal=cartItems.reduce((a, c) => a + c.price * c.qty, 0);
    const shippingcharges=subtotal<500? 50.00:0.00;
    return (
        <div>
            <div className="row top" style={{marginBottom:"3rem"}}>
                <h1>YOUR BAG</h1>
                {cartItems.length !== 0 ?
                <div className="continueshopping" style={{padding:"1rem "}}>
                <Link to="/">Continue Shopping &#8594;</Link>
                </div>:null}
                </div>
        <div className="row top">
            <div className="col-2">
                {
                cartItems.length === 0 ? 
                <div className=" centermeifnoitemincart">
                    <h1>Nothing in the bag yet!</h1>
                    <span style={{textDecoration:"underline" ,fontSize:"3rem"}}><Link to="/">Continue Shopping &#8594;</Link></span>
                    </div>:
                    <>
                        <div> {cartItems.length} Items</div>
                        <ul>
                            {
                                cartItems.map((item) => (
                                    <li key={item.product} className="itemincartlist">
                                        <div className="row">
                                            <div>
                                        <Link to={`/product/${item.product}`}>
                                                <img className="small" src={item.image} alt={item.name}></img>
                                            </Link>
                                            </div>
                                            <div className="min-30">
                                                <Link to={`/product/${item.product}`}>
                                                    {item.name}</Link>
                                            </div>
                                            <div className="row">
                                                <div>Qty</div>
                                                <div>
                                                    <select
                                                        value={item.qty}
                                                        onChange={e => dispatch(addToCart(item.product,
                                                            Number(e.target.value))
                                                        )}>
                                                        {[...Array(item.countInStock).keys()].map((x) => (
                                                            <option key={x + 1} value={x + 1}>
                                                                {x + 1}
                                                            </option>
                                                        ))}
                                                    </select>
                                                </div>
                                            </div>
                                            <div>&#8377; {item.price}</div>
                                            <div>
                                                <button type="button" onClick={() => removeFromCartHandler(item.product)}>
                                                    X Remove
                                            </button>
                                            </div>
                                        </div>
                                    </li>
                                ))
                            }
                        </ul>
                    </>
                }
            </div>
            {cartItems.length !== 0 ?
            <div className="col-1">
                <div style={{paddingLeft:"1rem"}}>Order Summary</div>
                <div className="card card-body">
                    <ul>
                        <div className="row">
                            <li> Subtotal ({cartItems.reduce((a, c) => a + c.qty, 0)} items)</li>
                            <li> &#8377; {cartItems.reduce((a, c) => a + c.price * c.qty, 0)}</li>
                        </div>
                        
                        <div className="row">
                            <li>Shipping Charges</li>
                            <li>&#8377; {shippingcharges}</li>
                        </div>
                        </ul>
                        <hr/>
                        <ul>
                        <li><h2>Total :  &#8377; {subtotal+shippingcharges}</h2></li>

                        <li>
                            <button type="button" onClick={checkoutHandler} className="primary block" disabled={cartItems.length === 0}>
                                Proceed to checkout</button>
                        </li>
                    </ul>
                </div>
            </div>
            :null}
        </div>
        </div>
    )
}

export default CartScreen
