import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { saveShippingAddress } from '../actions/cartAction';
import CheckoutSteps from './CheckoutSteps'

export default function ShippingAddressScreen(props) {

    const userSignin = useSelector(state => state.userSignin);
    const { userInfo } = userSignin;
    const cart = useSelector(state => state.cart);
    const { shippingAddress } = cart;
    if (!userInfo) {
        props.history.push('/signin');
    }

    const [fullName, setFullName] = useState(shippingAddress.fullName);
    const [address, setAddress] = useState(shippingAddress.address);
    const [city, setCity] = useState(shippingAddress.city);
    const [pincode, setPinCode] = useState(shippingAddress.pincode);
    const [landmark, setLandMark] = useState(shippingAddress.landmark);
    const [contactnumber, setContactNumber] = useState(shippingAddress.contactnumber);
    const [alternatecontactnumber, setAlternateContactNumber] = useState(shippingAddress.alternatecontactnumber);
    const dispatch = useDispatch();
    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(saveShippingAddress({ fullName, address, city, pincode, landmark, contactnumber, alternatecontactnumber }));
        props.history.push('/payment');
    }
    return (
        <div>
            <CheckoutSteps step1 step2 ></CheckoutSteps>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>Shipping Address</h1>
                </div>
                <div>
                    <label htmlFor="fullName" >Full Name</label>
                    <input type="text" id="fullName" placeholder="Enter full name" value={fullName}
                        onChange={(e) => setFullName(e.target.value)} required></input>
                </div>
                <div>
                    <label htmlFor="address" >Address</label>
                    <input type="text" id="address" placeholder="Enter address " value={address}
                        onChange={(e) => setAddress(e.target.value)} required></input>
                </div>
                <div>
                    <label htmlFor="city" >City</label>
                    <input type="text" id="city" placeholder="Enter City" value={city}
                        onChange={(e) => setCity(e.target.value)} required></input>
                </div>
                <div>
                    <label htmlFor="pincode" >Pincode</label>
                    <input type="text" id="pincode" placeholder="Enter Pincode" value={pincode}
                        onChange={(e) => setPinCode(e.target.value)} required></input>
                </div>
                <div>
                    <label htmlFor="landmark" >Landmark</label>
                    <input type="text" id="landmark" placeholder="Enter Landmark " value={landmark}
                        onChange={(e) => setLandMark(e.target.value)} required></input>
                </div>

                <div>
                    <label htmlFor="contactnumber" >Contact Number</label>
                    <input type="text" id="contactnumber" placeholder="Enter contact number" value={contactnumber}
                        onChange={(e) => setContactNumber(e.target.value)} required></input>

                </div>
                <div>
                    <label htmlFor="alternatecontactnumber" >Alternate Contact Number</label>
                    <input type="text" id="alternatecontactnumber" placeholder="Enter alternate contact number "
                        value={alternatecontactnumber} onChange={(e) => setAlternateContactNumber(e.target.value)} ></input>
                </div>
                <div>
                    <label />
                    <button className="primary" type="submit">Continue</button>
                </div>

            </form>
        </div>
    )
}
