import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { siginout } from "../actions/userAction";

function Header() {
  const [sidebar, setSidebar] = useState(false);
  const [clothing, setClothing] = useState(true);
  const showSidebar = () => {
    setSidebar(!sidebar);
  };

  const innerultoggleclothing = () => {
    if (clothing) {
      document.getElementById("Clothing").style.display = "block";
      setClothing(false);
    } else {
      document.getElementById("Clothing").style.display = "none";
      setClothing(true);
    }
  };
  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;
  const userSignin = useSelector((state) => state.userSignin);
  const { userInfo } = userSignin;
  const dispatch = useDispatch();
  const signoutHandler = () => {
    dispatch(siginout());
  };
  return (
    <>
      <header>
        <nav>
          <label style={{ fontFamily: "cursive"}}>
            <Link to="/">Queens</Link>
          </label>

          <ul>
            <li>
              {userInfo ? (
                <div className="dropdown">
                  <Link to="#">
                    {userInfo.name} <i className="fa fa-caret-down"></i>
                  </Link>
                  <ul className="dropdown-content">
                    <Link to="#signout" onClick={signoutHandler}>
                      Sign Out
                    </Link>
                  </ul>
                </div>
              ) : (
                <Link to="/signin">
                  <i className="far fa-user" style={{ color: "black" }}></i>
                </Link>
              )}
            </li>

            <li>
              <Link to="/cart">
                <i
                  className="fas fa-shopping-bag"
                  style={{ color: "black" }}
                ></i>
                {cartItems.length > 0 && (
                  <span className="badge">{cartItems.length}</span>
                )}
              </Link>
            </li>
          </ul>

          <Link to="#" className="menubar" onClick={showSidebar}>
            &#9776;
          </Link>
        </nav>
      </header>

      <aside>
        <div className={sidebar ? "side_menu active" : "side_menu"}>
          <ul className="outerul" onClick={showSidebar}>
            <li>
              <Link to="#" className="menubar">
                &times;
              </Link>
            </li>

            <li className="fromhead">
              {" "}
              <Link to="/signin">User</Link>
            </li>
            <li className="fromhead">
              <Link to="/cart">My Bag</Link>
            </li>
            <li>
              <Link to="/NewNow"> New Now</Link>
            </li>

            <li>
              <Link to="/comingsoon">Trending</Link>
            </li>

            <li onClick={innerultoggleclothing}>Clothing</li>
            <ul className="innerul " id="Clothing" style={{ display: "none" }}>
              <li>lehangas</li>
              <li>kurti</li>
              <li>kurti sets</li>
              <li>top</li>
              <li>sari</li>
              <li>anarkali sets</li>
              <li>Dupatas</li>
              <li>Gownas</li>
            </ul>
            <li>
              <Link to="/comingsoon">Ready to ship</Link>
            </li>

            <li style={{ color: "red", fontWeight: "600" }}>
              {" "}
              <Link to="/comingsoon">sale</Link>
            </li>
            <li>
              <Link to="/comingsoon">get customized</Link>
            </li>
            <li>
              <Link to="/comingsoon">quick view</Link>
            </li>
          </ul>
        </div>
      </aside>
    </>
  );
}

export default Header;
